.home{
    width: 100%;
    position: relative;
    
}

.home .item-container{
    width: 100%;
    background-color: #fff;
    margin: auto;
    padding: 10px 0;
    display: flex;
    flex-wrap: wrap;
}

.home .item-left{
    width:14%
}

.home .item-left .container{
    position: relative;     
    top: 10px;
    left: 15px;

}

.home .item-left .container .text{
    font-size: 30px;
    text-align: left;
    font-weight: bold;
    margin-bottom: 20px;
}

.home .item-left .container .size-buttons{
    position: relative;
    text-align: left;
}

.home .item-left .container .size-buttons .btn{
    background: #efefef;
    border: none;
    border-radius: 50%;
    width: 45px;
    height: 45px;
    margin: 5px;
    cursor: pointer;
    font-size: 12px;
}

.home .item-right{
    width:85%
}

.home  .filter-sort{
    display: flex;
    position: relative;
    align-items: center;
    justify-content: flex-end;
    right: 16px;
    height: 50px;
}

.home  .filter-sort .text{
    font-size: 30px;
}

.home .filter-sort .select{
    position: relative;
    margin-left: 20px;
    padding: 10px 10px;
    font-size: 16px;
}

.home  .filter-size{
    display: flex;
    position: relative;
    align-items: normal;
    justify-content: flex-start;
    left: 0;
    height: 40px;
}

.home  .filter-size .text{
    font-size: 30px;
}

.home .filter-size .select{
    position: relative;
    padding: 10px 10px;
    font-size: 16px;
}

.home .item-right .row{
    display: flex;
    flex-wrap: wrap;
    position: relative;
    width: 100%;
}

.home .item-right .row .product{
    width: 24%;
    margin: 10px 5px;
    border: 1px solid transparent;
    padding: 10px 10px;
    position: relative;
    cursor: pointer;
}

.home .item-right .row .product .image{
    transform: scale(1);
}

.home .product-details{position: relative;}

.home .item-right .row .product .image img{
    width: 200px;
}

.home .item-right .product-price .actualPrice{     
    font-size: 20px;
}
  
 .home .item-right .product-price .discount, .home .item-right .product-price .disSountcurrency{
    color:#9c9c9c;
    font-weight: 400;
    font-size: 14px;
 }
 
 .home .item-right .row .product .status{
    position: absolute;
    top: 10px;
    right: 5px;   
 }

 .home .item-right .row .product .status span{
    background: #000;
    color: #fff;
    font-size: 10px;
    padding: 5px 10px;
 }

.home .item-right .row .product .name{
    margin: 15px auto;
}

.home .item-right .row .product .line{
    position: relative;
    height: 3px;
    background: #ffc14f;
    display: block;
    width: 9%;
    text-align: center;
    border-radius: 10px;
    margin: 15px auto;
}

.home .item-right .row .product .add-to-cart{
    background: #000;
    padding: 17px 0px;
    width: 100%;
    color: #fff;
    text-transform: uppercase;
    font-size: 13px;
    text-align: center;
    margin-top: 20px;
    font-weight: bold;
  }

  .home .item-right .row .product:hover{
      border: 1px solid grey;
      transition: 0.3s;
  }
  .home .item-right .row .product:hover .add-to-cart{
    background: #f9cb02;   
    transition: 0.3s;
  }

  .home .item-right .total-Product{
    text-align: left;
    font-size: 20px;
    position: relative;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }