.cart-container{
    position: fixed;
    top:0;
    right: 0;
    background:#1b1a20;
    width: 470px;
    height:100%;
    transition: 0.3s;
}

.cart-container .cart-product{
    height: 350px;
    overflow-y: auto;
    overflow-x: hidden;
}

.cart-product .massage{
    color: #fff;
    font-size: 20px;
    text-align: center;
}

.cart-container.opened{
    right:0;
}

.cart-container.closed{
    right:-470px;
}

.cart-container .cart {
    background: #1b1a20;
    width: 60px;
    height: 60px;
    display: block;
    margin: 60px auto 20px;
    position: relative;
    padding: 10px 0 0;
}
.cart-container .cart .logo{
    background: url("./../assets/bag-icon.png") no-repeat;    
    width: 32px;
    height: 32px;
    overflow: hidden;
    margin: auto;
    display: flex;
    align-items: center;
    vertical-align: middle;  
}

.cart-container .cart .count{
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    left: 30px;
    top: -10px;
    width: 25px;
    height: 25px;
    background: #ea9327;
    border-radius: 50%;
    color: #fff;
    font-size: 12px;
    font-weight: bold;
}

.cart-container .cart .title{
    position: absolute;
    color: #fff;
    font-size: 24px;
    font-weight: bold;
    top: 10px;
    left: 70px;
}

.cart-container .image{
    margin: 0 20px;
    text-align: left;
    width: 17%;
}

.cart-container .image img{
    width:90px;
}

.cart-container .cart-details{
    border: 1px solid #000;
    padding: 20px 0;
    text-align: left;
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.cart-container .cart-info{
    position: relative;
    left: 0;
    text-align: left;
    width: 58%;
}

.cart-container .cart-info .name{
    color: #fff;
}

.cart-container .cart-info .size{
    color: #656469;
}

.cart-container .cart-info .quantity{
    color: #656469;
}

.cart-container .cart-details .cart-price-details{
    position: relative;
    width: 16%;
}

.cart-container .cart-details .cart-price-details .delete-product{
    position: relative;
    height: 30px;
}

.cart-container .cart-details .cart-price-details .delete-product .icon{
    background: url("./../assets/sprite_delete-icon.png") no-repeat;
    width:20px;
    height:20px;
    background-position: 5px 3px;
    display: block;
    cursor: pointer;
    position: absolute;
    right: 19px;
}

.cart-container .cart-details .cart-price-details .delete-product .icon:hover{
    background-position: -13px 3px;
}

.cart-container .cart-details .cart-price-details .actualPrice{
    position: relative;
    color: yellow;
    font-size: 20px;
    text-align: right;
    margin-bottom: 20px;
    right: 20px;
}

.cart-container .cart-details .cart-price-details .symbol{
    background: #000;
    color: #fff;
    font-size: 20px;
    padding: 2px 3px;
    cursor: pointer;
}

.cart-container .cart-details .cart-price-details .symbol.disabled{
    opacity: 0.3;
    cursor:default;
}

.cart-container .close-btn{
    font-size: 35px;
    position: absolute;
    background: #000;
    color: #fff;
    padding: 10px;
    left: -59px;
    display: flex;
    cursor: pointer;
}

.cart-container.closed .close-btn{
    display: none;
}

.cart-container.closed .cart{
    position: absolute;
    left: -59px;
    top:-60px;
}

.cart-container .cart-sumamry{
    box-shadow: 12px -4px 5px #040000;
    width: 100%;
    height: 150px;
    padding: 10px 20px;
    text-align: center;
    position: absolute;
    top: 500px;
    background: #1b1a20;
}

.cart-container .cart-sumamry .info{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 22px;
    width: 93%;
}

.cart-container .cart-sumamry .info .text{
    font-size: 20px;
    color: #fff;
}

.cart-container .cart-sumamry .info .total{
    color: yellow;
    position: relative;
    right: 0px;
}

.cart-container .cart-sumamry .checkout{
    width: 80%;
    background: #0e0d13;
    border: navajowhite;
    padding: 10px;
    color: #fff;
    font-size: 20px;
}
 
.cart-container .cart-sumamry .checkout.disabled{
    opacity: 0.2;
    cursor: default;
}