.topbar{
    position: relative;     
    width:100%;    
}

.topbar .main-container{
    width: 1300px;
    margin: 0 auto;
    width: 100%;
    display: flex;    
    align-items: center;
    justify-content: space-between;
}

.topbar .container{    
    width: 100%;
    display: flex;
    height: 70px;
    align-items: center;
    justify-content: space-between;
}

.topbar .container .left-logo{
    background: #efefef;
    color: #fff;
    width: 60px;
    height: 71px;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    font-size: 30px;
}
